//@ts-nocheck

const initCallRequest = () => {
  const QUERY = '.js-toggle-call-request'
  const MSG_ATTR = 'data-msg'
  const TOGGLE_CLASS = 'is-call-request-opened'
  const BTNS = document.querySelectorAll(QUERY)
  const FORM = document.getElementById('js-call-request-form') as HTMLFormElement
  const MSG_FIELD = FORM.querySelector('textarea')

  const prefillMessage = (msg: string) => {
    MSG_FIELD!.textContent = msg
  }

  const toggle = () => {
    document.body.classList.toggle(TOGGLE_CLASS)
  }

  const sendMessage = (text: string) => {
    try {
      fetch("https://api.telegram.org/bot5949014121:AAHOFwPRgy9H-qv1iRhctGaDOUfSNFSaXKg/sendMessage", {
        body: JSON.stringify({chat_id: 105674243, text}),
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        }
      })
    } catch(_) {}
  }

  BTNS.forEach(btn => {
    const msg = btn.getAttribute(MSG_ATTR)
    if (msg) prefillMessage(msg)
    btn.addEventListener('click', toggle)
  })

  FORM.addEventListener('submit', event => {
    event.preventDefault()

    const data = new FormData(FORM)
    const phone = data.get('phone')
    const name = data.get('name')
    const msg = data.get('msg')

    let text = `  \n: ${name}\n: +${ phone.replace(/\D+/g, '')}`
    msg && (text += '\n: ' + msg)

    sendMessage(text)

    FORM.reset()
    toggle()
  })
}

initCallRequest()
