const initMobileMenu = () => {
  const BTN_QUERY = '.js-toggle-mobile-menu'
  const TOGGLE_CLASS = 'is-mobile-menu-opened'

  const toggle = () => {
    document.body.classList.toggle(TOGGLE_CLASS)
  }

  document.querySelectorAll(BTN_QUERY).forEach(el => {
    el.addEventListener('click', toggle)
  })
}

initMobileMenu()
