import Swiper, { Autoplay, Navigation, EffectFade } from 'swiper'
import 'swiper/swiper.scss'
import 'swiper/modules/effect-fade/effect-fade.scss'

import './requestCall'
import './mobileMenu'
import './imask'

document.addEventListener("DOMContentLoaded", () => {
  const servicesCurrentSlideOut = document.getElementById('js-slider-current')

  new Swiper('#js-slider', {
    // autoplay: {
    //   delay: 2000,
    // },
    modules: [EffectFade, Navigation, Autoplay],
    effect: "fade",
    loop: true,
    navigation: {
      nextEl: "#js-slider-next-btn",
      prevEl: "#js-slider-prev-btn",
    },
    on: {
      slideChange: ({ realIndex }) => {
        if (servicesCurrentSlideOut)
          //@ts-ignore
          servicesCurrentSlideOut.innerText = realIndex + 1
      }
    }
  })

  new Swiper('#js-slider-cases', {
    slidesPerView: 'auto',
    spaceBetween: 40,
  })
})
